import { useEffect, useState } from 'react';
import './App.css';

import { ChatList } from './componets/ChatList/ChatList';
import { ChatWindow } from "./componets/ChatWindow/ChatWindow";
import { ChatIntro } from "./componets/ChatIntro/ChatIntro";
import { NewChat } from './componets/NewChat/NewChat'
import { Login } from "./componets/Login/Login";
import DonutLargeIcon from '@mui/icons-material/DonutLarge';
import ChatIcon from '@mui/icons-material/Chat';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import SearchIcon from '@mui/icons-material/Search';
import Api from "./Api";


const App = () => {
  window.onresize = () => {
    let w = document.body.clientWidth
    w <= 750 ? setMobile(true) : setMobile(false)
  }

  const [chatlist, setChatList] = useState([])
  const [activeChat, setActiveChat] = useState({})
  const [user, setUser] = useState(null)
  const [showNewChat, setShowNewChat] = useState(false)
  const [mobile, setMobile] = useState(false)

  useEffect(() => {
    let w = document.body.clientWidth
    w <= 750 ? setMobile(true) : setMobile(false)
  }, [])
  useEffect(() => {
    const getChatList = () => {
      if (user !== null) {
        let unsub = Api.onChatList(user.id, setChatList)
        return unsub
      }
    }
    getChatList()
  }, [user])

  const handleNewChat = () => {
    setShowNewChat(true)
  }

  const handleLoginData = async (u) => {
    let newUser = {
      id: u.uid,
      name: u.displayName,
      avatar: u.photoURL
    };
    await Api.addUser(newUser);
    setUser(newUser)
  }

  if (user == null) {
    return (<Login onReceive={handleLoginData} />)
  }

  return (
    <div className="app-window">
      <div className={`sidebar ${activeChat.chatId !== undefined && mobile ? 'disabled' : ''}`}>
        <NewChat
          chatlist={chatlist}
          user={user}
          show={showNewChat}
          setShow={setShowNewChat}
          setState={setActiveChat}
        />
        <header>
          <img className="header--avatar" src={user.avatar} alt="" />
          <div className="header--buttons">
            <div className="header--btn">
              <DonutLargeIcon style={{ color: '#AEBAC1' }} />
            </div>
            <div onClick={handleNewChat} className="header--btn">
              <ChatIcon style={{ color: '#AEBAC1' }} />
            </div>
            <div className="header--btn">
              <MoreVertIcon style={{ color: '#AEBAC1' }} />
            </div>
          </div>
        </header>
        <div className="search">
          <div className="search--input">
            <SearchIcon fontSize="small" style={{ color: '#AEBAC1' }} />
            <input type="search" placeholder="Procurar ou comear uma nova conversa" />
          </div>
        </div>
        <div className="chatlist">
          {chatlist.map((item, index) => (
            <ChatList
              key={index}
              data={item}
              active={activeChat.chatId === chatlist[index].chatId}
              onClick={() => setActiveChat(chatlist[index])}
            />
          ))}
        </div>
      </div>


      <div className={`contentarea ${mobile ? activeChat.chatId !== undefined ? '' : 'disabled' : ''}`}>
        {activeChat.chatId !== undefined &&
          <ChatWindow
            user={user}
            data={activeChat}
            setShow={setActiveChat}
            mobile={mobile}
          />
        }
        {activeChat.chatId === undefined &&
          <ChatIntro
            event={setActiveChat}
          />
        }
      </div>
    </div>
  );
}

export default App;
