import { useState, useEffect, useRef } from 'react';
import './style.css'
import { MensageItem } from '../MensageItem/MensageItem';
import Api from '../../Api';
import EmojiPicker from 'emoji-picker-react';
import SearchIcon from '@mui/icons-material/Search';
import AttachFileIcon from '@mui/icons-material/AttachFile';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import InsertEmoticonIcon from '@mui/icons-material/InsertEmoticon';
import CloseIcon from '@mui/icons-material/Close';
import SendIcon from '@mui/icons-material/Send';
import MicIcon from '@mui/icons-material/Mic';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

export const ChatWindow = ({ user, data, setShow, mobile }) => {
  const body = useRef()

  let recognition = null;
  let SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
  if (SpeechRecognition !== undefined) {
    recognition = new SpeechRecognition();
  }

  const [emojiOpen, setEmojiOpen] = useState(false);
  const [text, setText] = useState('');
  const [listening, SetListening] = useState(false)
  const [list, setList] = useState([])
  const [users, setUsers] = useState([])

  useEffect(() => {
    if (body.current.scrollHeight > body.current.offsetHeight) {
      body.current.scrollTop = body.current.scrollHeight - body.current.offsetHeight;
    }
  }, [list])
  useEffect(() => {
    setList([])
    let unsub = Api.onChatContent(data.chatId, setList, setUsers)
    return unsub
  }, [data.chatId])

  const handleEmojiClick = (e, emojiObject) => {
    setText(text + emojiObject.emoji)
  }
  const handleSendClick = () => {
    if (text !== '') {
      Api.sendMessage(data, user.id, 'text', text, users)
      setText('')
      setEmojiOpen(false)
    }
  }

  const handleInputKeyup = (e) => {
    if (e.keyCode === 13) {
      handleSendClick();
    }
  }
  const handleMicClick = () => {
    if (recognition !== null) {
      recognition.onstart = () => {
        SetListening(true)
      }
      recognition.onend = () => {
        SetListening(false)
      }
      recognition.onresult = (e) => {
        setText(e.results[0][0].transcript);
      }
      recognition.start();
    }
  }
  const handleClose = () => {
    setShow({})
  }

  return (
    <div className='chatWindow'>
      <div className='chatWindow--header'>
        <div className='chatWindow--headerinfo'>
          {mobile &&
            <div onClick={handleClose} className='chatWindow--headerbuttons'>
              <ArrowBackIcon style={{ color: '#AEBAC1' }} />
            </div>
          }
          <img className='chatWindow--avatar' src={data.image} alt="" />
          <div className='chatWindow--name'>{data.title}</div>
        </div>
        <div className='chatWindow--headerbuttons'>
          <div className='chatWindow-btn'>
            <SearchIcon style={{ color: '#AEBAC1' }} />
          </div>
          <div className='chatWindow-btn'>
            <MoreVertIcon style={{ color: '#AEBAC1' }} />
          </div>
        </div>
      </div>
      <div ref={body} className='chatWindow--body'>
        {list.map((item, index) => (
          <MensageItem
            key={index}
            data={item}
            user={user}
          />
        ))}
      </div>
      <div className='chatWindow--emojiarea' style={{ height: emojiOpen ? '300px' : '0px' }}>
        <EmojiPicker
          onEmojiClick={handleEmojiClick}
          disableSearchBar
          disableSkinTonePicker
        />
      </div>
      <div className='chatWindow--footer'>
        <div className='chatWindow--pre'>

          <div className='chatWindow-btn'
            onClick={() => setEmojiOpen(false)}
            style={{ width: emojiOpen ? '40px' : '0px' }}
          >
            <CloseIcon style={{ color: '#919191' }} />
          </div>

          <div className='chatWindow-btn'
            onClick={() => setEmojiOpen(true)}
          >
            <InsertEmoticonIcon style={{ color: emojiOpen ? '#009688' : '#919191' }} />
          </div>

          <div className='chatWindow-btn'>
            <AttachFileIcon style={{ color: '#919191' }} />
          </div>
        </div>
        <div className='chatWindow--inputarea'>
          <input
            className='chatWindow--input'
            type="text"
            placeholder='Digite uma mensagem'
            value={text}
            onChange={e => setText(e.target.value)}
            onKeyUp={handleInputKeyup}
          />
        </div>
        <div className='chatWindow--pos'>
          {!text &&
            <div onClick={handleMicClick} className='chatWindow-btn'>
              <MicIcon style={{ color: listening ? '#126ECE' : '#919191' }} />
            </div>
          }
          {text &&
            <div onClick={handleSendClick} className='chatWindow-btn'>
              <SendIcon style={{ color: '#919191' }} />
            </div>
          }
        </div>
      </div>
    </div>
  )
}