import { getAuth, signInWithPopup, FacebookAuthProvider } from "firebase/auth";
import { initializeApp } from "firebase/app";
import {
  getFirestore,
  doc,
  setDoc,
  addDoc,
  query,
  collection,
  updateDoc,
  getDocs,
  getDoc,
  arrayUnion,
  onSnapshot,
} from "firebase/firestore";

import { firebaseConfig } from "./firebaseConfig";
const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

const apiFunction = {
  fbPopup: async () => {
    const provider = new FacebookAuthProvider();
    const auth = getAuth();
    let result = await signInWithPopup(auth, provider);
    return result;
  },
  addUser: async (u) => {
    await setDoc(
      doc(db, "users", u.id),
      {
        name: u.name,
        avatar: u.avatar,
      },
      { merge: true },
    );
  },
  getContactList: async (userId) => {
    let list = [];
    const q = query(collection(db, "users"));

    const results = await getDocs(q);
    results.forEach((doc) => {
      let data = doc.data();
      if (doc.id !== userId) {
        list.push({
          id: doc.id,
          name: data.name,
          avatar: data.avatar,
        });
      }
    });
    return list;
  },
  addNewChat: async (user, user2) => {
    let newChat = await addDoc(collection(db, "chats"), {
      messages: [],
      users: [user.id, user2.id],
    });
    await updateDoc(doc(db, "users", user.id), {
      chats: arrayUnion({
        chatId: newChat.id,
        title: user2.name,
        image: user2.avatar,
        with: user2.id,
      }),
    });
    await updateDoc(doc(db, "users", user2.id), {
      chats: arrayUnion({
        chatId: newChat.id,
        title: user.name,
        image: user.avatar,
        with: user.id,
      }),
    });

    return newChat;
  },
  onChatList: (userId, setChatList) => {
    return onSnapshot(doc(db, "users", userId), (doc) => {
      if (doc.exists) {
        let data = doc.data();
        if (data.chats) {
          let chats = [...data.chats];
          chats.sort((a, b) => {
            if (a.lastMessageData === undefined) {
              return -1;
            }
            if (b.lastMessageData === undefined) {
              return -1;
            }
            if (a.lastMessageData.seconds < b.lastMessageData.seconds) {
              return 1;
            } else {
              return -1;
            }
          });

          setChatList(chats);
        }
      }
    });
  },
  onChatContent: (chatId, setList, setUsers) => {
    return onSnapshot(doc(db, "chats", chatId), (doc) => {
      if (doc.exists) {
        let data = doc.data();
        setList(data.messages);
        setUsers(data.users);
      }
    });
  },
  sendMessage: async (chatData, userId, type, body, users) => {
    let now = new Date();
    updateDoc(doc(db, "chats", chatData.chatId), {
      messages: arrayUnion({
        type,
        author: userId,
        body,
        date: now,
      }),
    });
    for (let i in users) {
      let docRef = doc(db, "users", users[i]);
      let docSnap = await getDoc(docRef);
      let uData = docSnap.data();
      if (uData.chats) {
        let chats = [...uData.chats];

        for (let e in chats) {
          if (chats[e].chatId === chatData.chatId) {
            chats[e].lastMessage = body;
            chats[e].lastMessageData = now;
          }
        }
        await updateDoc(doc(db, "users", users[i]), {
          chats,
        });
      }
    }
  },
};

export default apiFunction;
