import { useState, useEffect } from 'react';
import Api from '../../Api';
import './style.css'
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

export const NewChat = ({ user, chatlist, show, setShow, setState, }) => {
  const [list, setList] = useState([])
  useEffect(() => {
    const getList = async () => {
      if (user !== null) {
        let results = await Api.getContactList(user.id)
        setList(results)
      }
    }
    getList()
  }, [user])

  const handleClose = () => {
    setShow(false)
  }

  const addNewChat = async (user2) => {
    let arr = chatlist.filter((item) => item.with === user2.id)
    if (arr.length === 0) {
      await Api.addNewChat(user, user2)
      handleClose()
    } else {
      setState(arr[0])
      handleClose()
    }
  }
  return (
    <div className="newChat" style={{ left: show ? 0 : -1000 }}>
      <div className='newChat--head'>
        <div onClick={handleClose} className='newChat--backbutton'>
          <ArrowBackIcon style={{ color: '#FFF' }} />
        </div>
        <div className='NewChat--headTitle'>Nova Conversa</div>
      </div>
      <div className='newChat--list'>
        {list.map((item, index) => (
          <div onClick={() => addNewChat(item)} className='NewChat--item' key={index}>
            <img className='newChat--itemavatar' src={item.avatar} alt="" />
            <div className='newChat--itemArea'>
              <div className='newChat--itemname'>{item.name}</div>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}