import './style.css'
import { useEffect, useState } from 'react'

export const MensageItem = ({ data, user }) => {

  const [time, setTime] = useState('')

  useEffect(() => {
    if (data.date > 0) {
      let d = new Date(data.date.seconds * 1000)
      let h = d.getHours()
      let m = d.getMinutes()
      h = h < 10 ? '0' + h : h;
      m = m < 10 ? '0' + m : m
      setTime(h + ':' + m)
    }
  }, [data])

  return (
    <div className="menssageLine"
      style={{
        justifyContent: user.id === data.author ? 'flex-end' : 'flex-start'
      }}

    >
      <div className="mensageItem"
        style={{ backgroundColor: user.id === data.author ? '#005C4B' : '#202C33' }}
      >
        <div className="menssageText">{data.body}</div>
        <div className="menssageDate">{time}</div>
      </div>
    </div>
  )
}